.services{
    display: flex;
    height: 100vh;
    width: 100%;
    background-color: black;
    flex-direction: column;
    border-top-left-radius: 0px 100px;
    position: relative;
    overflow: hidden;
    padding: 0 5vw;
    box-sizing: border-box;
}
.services_title{
    color:white;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    width: 100%;
    box-sizing: border-box;
    position: relative;
    padding-bottom: 61px;

}
.services_title h1{
    font-family: 'Inter';
    font-style: italic;
    font-weight: 700;
    font-size: 66.2342px;
    line-height: 60px;
    margin-bottom: -12px;
}
.services_title h2{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 300;
    font-size: 38.1073px;
    line-height: 46px;
    letter-spacing: 0.355em;
    margin-right: -16px;
}
.services_container{
    display: flex;
    width: 100%;
    box-sizing: border-box;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 56.5px;
}
.services svg{
    position: absolute;
    filter: invert(1);
    bottom: -130px;
    width: 100vw;
}