.service{
    background-image: linear-gradient(103.82deg, #555 21.15%, #fff 36.77%, #555 64.37%);
    box-shadow: 0px 15.4154px 25.6923px 5.13846px rgba(0, 0, 0, 0.05);
    border-radius: 10.2769px;
    width: 326.29px;
    height: 161.86px;   
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    display: flex;
    flex-direction: column;
}
.service h1{
    position: relative;
    z-index: 2;
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    text-transform: uppercase;
}
.service h2{
    font-family: SpaceGrotesk;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: rgba(255, 255, 255, 0.6);
    position: relative;
    z-index: 2;
}
.service::before{
    content: "";
    background: linear-gradient(103.82deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0) 49%, rgba(255,255,255,0.1) 50%, rgba(0,0,0,0) 51%, rgba(0,0,0,0) 100%);
    background-size: 1000%;
    background-position: 0;
    box-shadow: 0px 15.4154px 25.6923px 5.13846px rgba(0, 0, 0, 0.05);
    border-radius: 10.2769px;
    width: 326.29px;
    height: 161.86px;   
    filter: brightness(4);
    z-index: 4;
    position: absolute;
    top: 0;
    animation: service-background 40s infinite linear;
    animation-delay: --delay;
}
.service:hover:before{
    animation: service-background 10s linear;
}
@keyframes service-background {
    0%{
        background-position: 1%;
    }
    50%{
        background-position: 500%;
    }
    100%{
        background-position: 1000%;
    }
}
.service:hover{
 border: solid 1px #888;   
}
.service::after{
    content: "";
    background-image: linear-gradient(103.82deg, #000000 21.15%, #121212 36.77%, #000000 64.37%);
    border-radius: 10.2769px;
    transform: scale(0.99);
    width: 100%;
    height: 100%;
    position: absolute;
}