.about{
    display: flex;
    width: 100%;
    height: calc(100vh - 70px);
    background-color: white;
    box-sizing: border-box;
    padding: 0 80px;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
}
.about_image{
    flex:1;
    display: flex;
    align-items: center;
    vertical-align: top;
    justify-content: center;
}
.about_text{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 27px;
    padding: 0 24px;
}
.about_text p{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 14.6639px;
    line-height: 18px;
}
.about .svg{
    position: absolute;
    bottom: -130px;
    width: 100vw;
}