nav {
    position: fixed;
    z-index: 10;
    top: 0;
    width: 100vw;
    background-color: #000;
    padding: 19px 98px 19px 39px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
}
.nav_logo_container{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
nav img{
    width: 55px;
    height: 32.82px;
    filter: brightness(1);
    transition: all 500ms;
}
nav img:hover{
    filter:brightness(2);
}
.nav_item_container{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.nav_menu_item {
    font-weight: 200;
    font-size: 14.6639px;
    line-height: 18px;
    text-decoration: none;
    color: #fff;
    transition: all 0.1s;
    text-align: center;
    text-transform: uppercase;

    @media screen and (max-width:700) {
        
    }
}

.nav_menu_item:hover {
    font-weight: 600;
}