.hero_image {
    display: flex;
    box-sizing: border-box;
    height:fit-content;
    width: 100%;
    height: 100%;
    padding-top:71px ;
    position: relative;
    transition: all 500ms;
}

.hero_image_text {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    justify-content: center;
    color: #FFFFFF;
    height: 100%;
    width: 100%;
    position: relative;
    z-index: 2;
}

.hero_image_text h1 {
    font-family: Inter;
    font-style: italic;
    font-weight: 100;
    font-size: 43.7689px;
    line-height: 53px;
    text-transform: uppercase;
}

.hero_image_text h2 {
    text-transform: uppercase;
    font-family: Inter;
    font-style: italic;
    font-weight: 100;
    font-size: 34.4679px;
    line-height: 42px;
    letter-spacing: 0.07em;
    text-align: left;
}

.hero_image_text .button {
    border: 0.5px solid #FFFFFF;
    border-radius: 161px;
    background-color: black;
    width: -moz-fit-content;
    width: fit-content;
    transition: all 0.5s;
    padding:10px 34px;
    
    font-family: Inter;
    font-style: italic;
    font-weight: 100;
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
    @media screen and (max-width:1300px) {
    
    }
}


.hero_image_text .button:hover{
    padding: 10px 60px;
    @media screen and (max-width:1300px) {
    }
}

.hero_image_btn_hover {
    animation: hero_image_btn 0.5s;
}

@keyframes hero_image_btn {
    10% {
        transform: translateX(-10px);
    }

    85% {
        transform: translateX(5px)
    }

    100% {
        transform: translateX(0px);
    }
}

.hero_image_img {
    flex: 1;
    width: 100%;
    box-sizing: border-box;
    height: auto;
    position: relative;
    align-items: center;
    justify-content: center;
    vertical-align: center;
    display:flex;
}

.hero_image_img .img {
    position: absolute;
    height: 30vw;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}
.hero_image_img .img img{
    height: 100%;
}
.hero_image_img .ball {
    background-color: white;
    position: absolute;
    border-radius: 50%;
}
.hero_image_img .gradient_ball{
    background: linear-gradient(0deg, rgba(255,255,255,1) 0%, rgba(0,0,0,0) 50%);
    position: absolute;
    border-radius: 50%;

}
.hero_image .floating_ball1{
    height: 5vw;
    width: 5vw;
    border-radius: 50%;
    border:1px solid white ;
    position: absolute;
    bottom: 3vw;
    left: 2vw;
    transition: all 500ms cubic-bezier(0.47, 1.64, 0.86, 0.79);
}
.hero_image .floating_ball2{
    height: calc(5vw*2.58);
    width: calc(5vw*2.58);
    border-radius: 50%;
    border:1px solid white ;
    position: absolute;
    left: 41vw;
    transition: all 500ms cubic-bezier(0.47, 1.64, 0.86, 0.79);
}
.hero_image .floating_ball3{
    height: calc(5vw*2.81);
    width: calc(5vw*2.81);
    border-radius: 50%;
    border:1px solid white ;
    position: absolute;
    left: -10vw;
    transition: all 500ms cubic-bezier(0.47, 1.64, 0.86, 0.79);
}